import { React, useEffect, useState } from 'react';
import { Card, CardHeader, CardContent, CardActions, CardMedia } from "@material-ui/core"
import { Avatar, Typography, IconButton } from "@material-ui/core"
import { makeStyles, useTheme } from '@material-ui/core/styles';
import InfoIcon from '@material-ui/icons/Info';

function ProjectCard(props) {
    const { section, item, handleOpenFn, ...other } = props;

    const useStyles = makeStyles((theme) => ({
        root:{
            minWidth: 250,
            maxWidth: 500,
            float: 'left',
            margin: '10px',
            border: '2px solid black'
        },
        media: {
            height: 0,
            paddingTop: '56.25%', // 16:9
        },
        avatar: {
            backgroundColor: '#FF0000',
        },
    }));
    const classes = useStyles()
    const theme = useTheme();
    
    return(
        <Card className={classes.root}>
            <CardHeader
                title={item.Title}
                subheader={item.Subtitle}
            >
                <Typography variant="header3" color="textPrimary" component="h3">{item.Dates}</Typography>
            </CardHeader>
            <CardMedia className={classes.media}
                image={"./"+item.Image}/>
            <CardContent>
            <Typography variant="body2" color="textSecondary" component="p">{item.Desc}</Typography>
            </CardContent>
            <CardActions>
                <IconButton>
                    <InfoIcon onClick={() => handleOpenFn(section, item)}/>
                </IconButton>
                <Avatar>P</Avatar>
            </CardActions>
        </Card>
    );
}

export default ProjectCard;